import React, { useState } from 'react';
import {
  ChakraProvider,
  Box,
  Text,
  Button,
  VStack,
  HStack,
  Grid,
  theme,
  Tabs,
  TabList,
  Tab,
  TabPanels,
  TabPanel,
} from '@chakra-ui/react';
import { ColorModeSwitcher } from './ColorModeSwitcher';
import { login } from './blockchain/functions';
import NewTokenForm from './components/newTokenForm';
import TokenList from './components/tokenList';

function App() {
  const [user, setUser] = useState('');

  const handleLogin = async () => {
    let result = await login();
    if (result) {
      setUser(result);
    }
  };

  return (
    <ChakraProvider theme={theme}>
      <Box textAlign="center" fontSize="xl">
        <HStack justifyContent="space-between">
          <ColorModeSwitcher justifySelf="flex-end" />
          <Button onClick={handleLogin}>Connect with Metamask</Button>
        </HStack>
        <Grid minH="100vh" p={3}>
          {user ? (
            <VStack spacing={8}>
              <Text>Tokenizer</Text>
              <Text>Welcome {user}</Text>

              <Tabs width="100%">
                <TabList justifyContent="center">
                  <Tab>Issue an Asset</Tab>
                  <Tab>Tokens Created</Tab>
                </TabList>

                <TabPanels>
                  <TabPanel>
                    <NewTokenForm />
                  </TabPanel>
                  <TabPanel>
                    <TokenList />
                  </TabPanel>
                </TabPanels>
              </Tabs>
            </VStack>
          ) : (
            <VStack spacing={8}>
              <Text>Tokenizer</Text>

              <Text> Please Login With Metamask</Text>
            </VStack>
          )}
        </Grid>
      </Box>
    </ChakraProvider>
  );
}

export default App;
